@import "../../var";

.post__wrap {
  min-height: 768px;
  padding: 3em;
}
.post {
  text-align: right;
  padding: 2em;
  min-height: 100px;
  span {
    margin: 0 0.5em;
    cursor: pointer;
    &:hover {
      color: blue;
    }
    a {
      color: $tur;
    }
    a:hover {
      text-decoration: underline;
    }
  }
}

.post__form {
  width: 80%;
  margin: 3em auto;

  display: grid;
  gap: 2em;
  grid-template-columns: repeat(3, 1fr);
  @media #{$medium} {
    grid-template-columns: repeat(2, 1fr);
  }
  @media #{$phone} {
    grid-template-columns: repeat(1, 1fr);
  }

  .post__form-title {
    border-bottom: 1px solid $blackShadow;
    margin-bottom: 1em;
    padding-bottom: 0.4em;
    color: $font-black;
  }
  .post__form-desc {
    padding-bottom: 1em;
    display: block;
    padding: 1em;
    height: 300px;
    max-height: 300px;
    word-break: break-word;
    white-space: pre-wrap;
    overflow: hidden;
    color: $font-black;
    &:hover {
      text-decoration: underline;
    }
  }
  .post__form-date {
    border-bottom: 1px solid $blackShadow;
    padding-bottom: 1em;
    text-align: right;
    margin-bottom: 2em;
    color: $dark-grey;
  }
}

.post__form-page {
  width: 100%;
  margin: 0 auto;
  text-align: center;
}
